.card {
  display: flex;
  flex-direction: column;
  background-color: var(--bg-color);
  padding: 20px;
  &__img {
    display: inline-block;
    margin-bottom: 20px;
    width: 100%;
  }
  &__name {
    margin-bottom: 10px;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    color: var(--light-green);
    @include small-tablet {
      font-size: 16px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 35px;
    color: var(--shark);
    @include small-tablet {
      font-size: 24px;
    }
  }

  &__info {
    margin: 0;
    margin-bottom: 30px;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    color: var(--shark);
    @include small-tablet {
      font-size: 16px;
    }
  }

  &__price {
    display: inline-block;
    margin-bottom: 20px;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: var(--green);
    @include small-tablet {
      font-size: 20px;
    }
  }

  &__btn {
    width: 100%;
  }
}
