.nav {
  &__list {
    display: flex;
    gap: 60px;
    @include small-tablet {
      transform: translateX(-100%);
      position: fixed;
      top: 175px;
      left: 0;
      width: 100%;
      height: 100vh;
      flex-direction: column;
      padding-left: 15px;
      background-color: var(--bg-color);
      transition: transform 0.3s ease-in-out;
    }
  }
  &__link {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
    color: var(--shark);
    transition: color 0.3s ease-in-out;
    @include hover {
      color: var(--dark-green);
    }
    &:active {
      color: var(--dark-green);
    }
  }
}
.menu--active {
  transition: transform 0.3s ease-in-out;
  transform: translateX(0);
}
