.team {
  padding: 70px 0;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
    @include small-tablet {
      flex-direction: column;
    }
  }

  &__content {
    max-width: 635px;
    padding-top: 50px;
    @include tablet {
      max-width: 510px;
      padding-top: 0;
    }
    @include small-tablet {
      max-width: none;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    line-height: 110%;
    color: var(--shark);
    @include mobile {
      font-size: 24px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    color: var(--shark);
    @include mobile {
      font-size: 16px;
    }
  }

  &__list {
    display: flex;
    gap: 30px;
  }
}
