@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@500;700&display=swap");
:root {
  --font-family: "Raleway", sans-serif;
  --content-width: 1140px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --white: #fff;
  --bg-color: #e1ede6;
  --green: #598d66;
  --dark-green: #376b44;
  --light-green: #86928b;
  --shark: #2c2d35;
  --yellow: #eedb6d;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable value-keyword-case */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn {
  padding: 21px 60px;
  border: 1px solid var(--green);
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  color: var(--green);
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}
@media (any-hover: hover) {
  .btn:hover {
    background-color: var(--green);
    color: var(--white);
  }
}
@media (max-width: 576px) {
  .btn {
    font-size: 18px;
  }
}

.header {
  padding: 38px 0;
  background-color: var(--bg-color);
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 30px;
}
.header__content {
  display: flex;
  align-items: center;
  gap: 60px;
  position: relative;
}
@media (max-width: 768px) {
  .header__content {
    width: 100%;
    justify-content: space-between;
  }
}
@media (max-width: 768px) {
  .header__logo {
    flex: 1 1 auto;
    text-align: center;
  }
}

.burger {
  z-index: 15;
  --burger-width: 20px;
  --burger-height: 16px;
  --burger-line-height: 2px;
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;
  display: none;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}
@media (max-width: 768px) {
  .burger {
    display: block;
  }
}

.nav__list {
  display: flex;
  gap: 60px;
}
@media (max-width: 768px) {
  .nav__list {
    transform: translateX(-100%);
    position: fixed;
    top: 175px;
    left: 0;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    padding-left: 15px;
    background-color: var(--bg-color);
    transition: transform 0.3s ease-in-out;
  }
}
.nav__link {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  color: var(--shark);
  transition: color 0.3s ease-in-out;
}
@media (any-hover: hover) {
  .nav__link:hover {
    color: var(--dark-green);
  }
}
.nav__link:active {
  color: var(--dark-green);
}

.menu--active {
  transition: transform 0.3s ease-in-out;
  transform: translateX(0);
}

.cart {
  position: relative;
}
.cart__icon {
  width: 24px;
  height: 24px;
  transition: fill 0.3s ease-in-out;
}
.cart__icon:focus {
  outline: none;
  background: var(--dark-green);
}
@media (any-hover: hover) {
  .cart__icon:hover {
    fill: var(--green);
  }
}
.cart__icon:active {
  fill: var(--dark-green);
}
.cart__quantity {
  display: none;
  position: absolute;
  top: 2px;
  left: 0;
  background-color: var(--green);
  border-radius: 50%;
  padding: 1px 4px;
  font-style: normal;
  font-weight: 700;
  font-size: 10px;
  line-height: 12px;
  text-align: center;
  color: var(--white);
}

.hero__container {
  display: flex;
  gap: 30px;
  align-items: center;
}
@media (max-width: 576px) {
  .hero__container {
    flex-direction: column;
  }
}
.hero__content {
  max-width: 540px;
  padding: 30px 0;
}
@media (max-width: 768px) {
  .hero__content {
    max-width: 285px;
  }
}
@media (max-width: 576px) {
  .hero__content {
    max-width: none;
    padding: 0;
  }
}
@media (max-width: 576px) {
  .hero__img {
    display: none;
  }
}
.hero__title {
  margin: 0;
  margin-bottom: 20px;
  font-style: normal;
  font-weight: 500;
  font-size: 60px;
  line-height: 110%;
  color: var(--shark);
}
.hero__title span {
  color: var(--green);
}
@media (max-width: 768px) {
  .hero__title {
    font-size: 36px;
  }
}
.hero__text {
  margin: 0;
  margin-bottom: 60px;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: var(--light-green);
}

.reproductions {
  padding: 70px 0;
}
.reproductions__header {
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 20px;
}
.reproductions__title {
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 110%;
  color: var(--shark);
}
.reproductions__tabs {
  display: flex;
  gap: 32px;
  overflow: auto;
}
.reproductions__tab {
  padding: 10px 20px;
  background-color: var(--bg-color);
  border-radius: 20px;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  color: var(--shark);
}
.reproductions__tab--active {
  background-color: var(--dark-green);
  color: var(--white);
}
.reproductions__content {
  display: none;
}
.reproductions__content--active {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
}
@media (max-width: 576px) {
  .reproductions__content--active {
    display: flex;
    flex-direction: column;
  }
}
.reproductions__card {
  grid-column: 4 span;
}
@media (max-width: 1024px) {
  .reproductions__card {
    grid-column: 6 span;
  }
}

.card {
