.footer {
  padding: 23px 0;
  background-color: var(--bg-color);
  &__container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 40px;
  }

  &__col {
    display: flex;
    flex-wrap: wrap;
    &--left {
      flex-direction: column;
    }
    &--center {
      gap: 60px;
    }
    &--right {
      flex-direction: column;
      max-width: 132px;
      display: flex;
      gap: 10px;
      @include mobile {
        max-width: none;
        justify-content: center;
      }
    }
  }
  &__logo {
    margin-bottom: 35px;
  }
  &__tel {
    margin-bottom: 8px;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
    color: var(--shark);
    @include hover {
      color: var(--dark-green);
    }
  }

  &__text {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    color: var(--light-green);
    transition: color 0.3s ease-in-out;
    @include hover {
      color: var(--dark-green);
    }
  }

  &__name {
    margin: 0;
    margin-bottom: 6px;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
    color: var(--shark);
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 6px;
  }

  &__ink {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 120%;
    color: var(--light-green);
  }
}
.footer-icon {
  display: flex;
  justify-content: space-between;
  gap: 5px;
  &__svg {
    width: 24px;
    height: 24px;
    transition: fill 0.3s ease-in-out;
    @include hover {
      fill: var(--dark-green);
    }
  }
}
