// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Raleway", sans-serif;
  --content-width: 1140px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --white: #fff;
  --bg-color: #e1ede6;
  --green: #598d66;
  --dark-green: #376b44;
  --light-green: #86928b;
  --shark: #2c2d35;
  --yellow: #eedb6d;
}
