.reproductions {
  padding: 70px 0;
  &__header {
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
  }

  &__title {
    margin: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    line-height: 110%;
    color: var(--shark);
  }

  &__tabs {
    display: flex;
    gap: 32px;
    overflow: auto;
  }
  &__tab {
    padding: 10px 20px;
    background-color: var(--bg-color);
    border-radius: 20px;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    color: var(--shark);
    &--active {
      background-color: var(--dark-green);
      color: var(--white);
    }
  }

  &__content {
    display: none;
    &--active {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 30px;
      @include mobile {
        display: flex;
        flex-direction: column;
      }
    }
  }

  &__card {
    grid-column: 4 span;
    @include tablet {
      grid-column: 6 span;
    }
  }
}
