.cart {
  position: relative;
  &__icon {
    width: 24px;
    height: 24px;
    transition: fill 0.3s ease-in-out;
    &:focus {
      outline: none;
      background: var(--dark-green);
    }

    @include hover {
      fill: var(--green);
    }

    &:active {
      fill: var(--dark-green);
    }
  }
  &__quantity {
    display: none;
    position: absolute;
    top: 2px;
    left: 0;
    background-color: var(--green);
    border-radius: 50%;
    padding: 1px 4px;
    font-style: normal;
    font-weight: 700;
    font-size: 10px;
    line-height: 12px;
    text-align: center;
    color: var(--white);
  }
}
