.hero {
  &__container {
    display: flex;
    gap: 30px;
    align-items: center;
    @include mobile {
      flex-direction: column;
    }
  }

  &__content {
    max-width: 540px;
    padding: 30px 0;
    @include small-tablet {
      max-width: 285px;
    }
    @include mobile {
      max-width: none;
      padding: 0;
    }
  }
  &__img {
    @include mobile {
      display: none;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-style: normal;
    font-weight: 500;
    font-size: 60px;
    line-height: 110%;
    color: var(--shark);
    span {
      color: var(--green);
    }
    @include small-tablet {
      font-size: 36px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 60px;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    color: var(--light-green);
  }
}
