.header {
  padding: 38px 0;
  background-color: var(--bg-color);
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 30px;
  }

  &__content {
    display: flex;
    align-items: center;
    gap: 60px;
    position: relative;
    @include small-tablet {
      width: 100%;
      justify-content: space-between;
    }
  }
  &__logo {
    @include small-tablet {
      flex: 1 1 auto;
      text-align: center;
    }
  }
}
.burger {
  z-index: 15;
  @include burger;
  display: none;
  @include small-tablet {
    display: block;
  }
}
