.collection {
  padding: 70px 0;
  background-image: url(../img/collection-bg.jpg);
  @include bg-cover;
  &__content {
    max-width: 540px;
    @include small-tablet {
      max-width: none;
    }
  }

  &__title {
    position: relative;
    padding-left: 100px;
    margin: 0;
    margin-bottom: 20px;
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    line-height: 100%;
    color: var(--white);
    @include small-tablet {
      font-size: 30px;
      max-width: 450px;
    }
    @include mobile {
      font-size: 24px;
      padding-left: 60px;
    }
    &--icon {
      background-image: url(../img/star.svg);
      background-position: left center;
      background-repeat: no-repeat;
      background-size: 80px 80px;
      @include small-tablet {
        background-size: 60px 60px;
      }
      @include small-tablet {
        background-size: 50px 50px;
      }
    }
  }
  &__info {
    margin-bottom: 60px;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  &__text {
    margin: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    color: var(--white);
    @include small-tablet {
      font-size: 15px;
    }
  }

  &__btn {
    padding: 21px 60px;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
    background-color: var(--yellow);
    color: var(--shark);
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
    @include hover {
      color: var(--white);
      background-color: var(--dark-green);
    }
    @include mobile {
      font-size: 18px;
    }
  }
}
